<template>
  <div class="registerWrapper" id="registerBackground">
    <div class="formWrapper">
      <el-card class="box-card" style="border-radius: 20px;">
      <h1 class="registerTitle">注册</h1>
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="50px"
        class="ruleForm"
        hide-required-asterisk
      >
        <el-form-item prop="username">
          <el-input
            prefix-icon="el-icon-user"
            v-model="ruleForm.username"
            placeholder="用户名"
          ></el-input>
        </el-form-item>
        <el-form-item prop="telephone">
          <el-input
            prefix-icon="el-icon-mobile-phone"
            v-model="ruleForm.telephone"
            placeholder="手机号"
          ></el-input>
        </el-form-item>
        <el-form-item prop="email">
          <el-input
              prefix-icon="el-icon-message"
              v-model="ruleForm.email"
              placeholder="邮箱"
          ></el-input>
        </el-form-item>
        <el-form-item prop="password1">
          <el-input
            prefix-icon="el-icon-lock"
            v-model="ruleForm.password1"
            placeholder="请输入密码"
            show-password
          ></el-input>
        </el-form-item>
        <el-form-item prop="password2">
          <el-input
            prefix-icon="el-icon-lock"
            v-model="ruleForm.password2"
            placeholder="请再次输入密码"
            show-password
          ></el-input>
        </el-form-item>
        <el-form-item prop="code">
          <div>
            <el-input
                    prefix-icon="el-icon-message"
                    v-model="ruleForm.code" 
                    placeholder="验证码"
                    style="width: 120px;margin-left: -10px;"></el-input>
            <el-button
                      v-if="!isCountDownShow"
                      type="primary"
                      style="width:175px;margin-left: 10px;"
                      @click="getCode">获取验证码</el-button>
            <el-button type="primary"  style="width:175px;margin-left: 10px;" v-else>{{ countDownSecond }}s</el-button>
          </div>
          
        </el-form-item>
        <el-form-item class="registerButtonWrapper">
          <el-button
            class="registerButton"
            type="primary"
            @click="submitForm('ruleForm')">注 册</el-button>
            <el-button   type="primary" class="cancleButton" @click="cancle">去 登 录 </el-button>
        </el-form-item>
      </el-form>
      </el-card>
    </div>
  </div>
</template>

<script>
import { addUser } from "@/request/user.js";
import { sendRegisterEmail } from "@/request/user.js";
// 倒计时名称
let timer;

export default {
  name: "Register",
  data() {
    return {
       // 倒计时秒数
       countDownSecond: 60,
      // 是否显示秒数
      isCountDownShow: false,
      ruleForm: {
        username: "",
        telephone: "",
        email:"",
        password1: "",
        password2: "",
        code:"",
      },
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
        ],
        password1: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            min: 5,
            max: 20,
            message: "长度在 5 到 20 个字符",
            trigger: "blur",
          },
        ],
        password2: [
          { required: true, message: "请再次输入密码", trigger: "blur" },
          {
            min: 5,
            max: 20,
            message: "长度在 5 到 20 个字符",
            trigger: "blur",
          },
        ],
        telephone: [
          { required: true, message: "请输入手机号", trigger: "blur" },
          { min: 11, max: 11, message: "请输入11位手机号", trigger: "blur" },
        ],
        email: [
          {required: true, message: '请输入邮箱地址', trigger: 'blur'},
          {type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change']}
        ],
        code: [
          { required: true, message: "请输入验证码", trigger: "blur" },
          { min: 6, max: 6, message: "请输入6位验证码", trigger: "blur" },
        ],
      },
    };
  },
  created() {
    if (this.$store.getters.isLogin) {
      // 用户若已登录，自动跳转到首页
      this.$notify({
          title: '成功',
          message: '您已登录！已跳转到首页',
          type: 'success'
      })
      this.$router.replace({name: "Home",query: { fileType: 0, filePath: "/" },});
    }
  },
  methods: {
    cancle(){
        this.$router.replace('/login');
    },
    //  注册按钮-点击事件
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        //    校验表单
        if (valid) {
          //  各项校验通过-调用注册接口，传参用户名、手机号和密码
          addUser(this.ruleForm).then((res) => {
            if (res.success) {
              this.$notify({
                title: "成功",
                message: "注册成功！已跳转到登录页面",
                type: "success",
              });
              this.$refs[formName].resetFields(); // 注册成功之后清空表单
              this.$router.replace({ path: "/login" }); // 注册成功之后跳转到登录页面
            } else {
              this.$message.error(res.message); //  显示接口返回的错误信息
            }
          });
        } else {
          // 表单校验没通过
          this.$message.error("请完善信息！");
          return false;
        }
      });
    },

    async getCode(){
    // 各项校验通过-调用登录接口
    sendRegisterEmail(this.ruleForm.email).then((res) => {
          if (res.success) {
            console.log(res);
            this.startCountDown();
            this.$message.success("验证码发送成功！");
            this.isCountDownShow=true;
          } else {
            this.isCountDownShow=false;
            this.$message.error("验证码发送失败！");
          }
        });
  },
    // 倒计时
    startCountDown() {
      this.countDownSecond = 60;
      timer = setInterval(() => {
        this.countDownSecond--;
        if (this.countDownSecond == 0) {
          clearInterval(timer);
          this.isCountDownShow = false;
        }
      }, 1000);
    },
  },
};
</script>

<style lang="stylus" scoped>

.box-card{
  position: relative;
  width: 400px;
  margin-left:30px;
  margin-top: 20px;
  height: 530px;

}
.registerWrapper {
  background-image: url("~@/assets/xt.jpg");
  width:100%;
  height: 100%;
  position: fixed;
  background-size: 100% 100%;
}
.formWrapper {
  text-align: center;
}
.registerTitle {
  margin-bottom: 10px;
  font-weight: 300;
  font-size: 30px;
  color: #000;
}

.ruleForm {
  width: 100%;
  margin-top: 20px;
}

.registerButton {
  margin-right:100px;
  width: 100px;
}
.cancleButton {
 
  width: 100px;
}

</style>