<template>
  <div class="resetWrapper">
  <div class="formWrapper">
    <el-card class="box-card" style="border-radius: 20px;">
      <h1 class="resetTitle">修改密码</h1>
      <el-form 
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm">
          <el-form-item   prop="email">
            <el-input 
            placeholder="请输入邮箱"
              prefix-icon="el-icon-message"
              v-model="ruleForm.email" 
              autocomplete="off" 
              style="width: 250px;">
            </el-input>
          </el-form-item>
          <el-form-item   prop="code">
            <div>
              <el-input
              placeholder="验证码"
                prefix-icon="el-icon-message"
                v-model="ruleForm.code" 
                autocomplete="off" 
                style="width: 130px;margin-left: 0px;">
              </el-input>
              <el-button
                v-if="!isCountDownShow"
                type="primary"
                style="width:110px;margin-left: 10px;"
                @click="getCode">获取验证码</el-button>
              <el-button type="primary"  style="width:110px;margin-left: 10px;" v-else>{{ countDownSecond }}s</el-button>
            </div>
              
          </el-form-item>
          <el-form-item  prop="newPassword">
            <el-input 
            placeholder="请输入新密码"
              prefix-icon="el-icon-lock"
              v-model="ruleForm.newPassword" 
              autocomplete="off" 
              show-password 
              style="width: 250px;">
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-button  @click="cancle">取 消</el-button>
          <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
          </el-form-item>
        </el-form>
    </el-card>
  </div>
</div>
</template>



<script >
import { resetGetcode,doreset} from "@/request/user.js"; 

let timer;
export default {
  name: "Login",
  data() {
    return {
       // 倒计时秒数
       countDownSecond: 60,
      // 是否显示秒数
      isCountDownShow: false,
      formLabelWidth: '125px',
      ruleForm: {
          email: '',
          code: '',
          newPassword:''
        },

      rules: {
        email: [
          {required: true, message: '请输入邮箱地址', trigger: 'blur'},
          {type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change']}
        ],
        code: [
          { required: true, message: "请输入验证码", trigger: "blur" },
          { min: 6, max: 6, message: "请输入6位验证码", trigger: "blur" },
        ],
        newPassword: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 5, max: 20, message: "长度在 5 到 20 个字符", trigger: "blur",},
        ],
      },
    };
  },
  methods: {
    cancle(){
        this.$router.replace('/login');
    },
    //  登录按钮-点击事件
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // 各项校验通过-调用修改密码接口
          doreset(this.ruleForm, true).then((res) => {
            console.log(res);
            if (res.success) {
              this.$message.success("密码修改成功！");
              this.$refs[formName].resetFields(); //    清空表单项
              this.$router.replace({ name: "Login" }); // 跳转到登录页
            } else {
              this.$message.error(res.message);
            }
          });
        } else {
          this.$message.error("请检查信息是否正确！");
          return false;
        }
      });
    },

    async getCode(){
      
      // 各项校验通过-调用登录接口
      resetGetcode(this.ruleForm.email).then((res) => {
            if (res.success) {
              console.log(res);
              this.startCountDown();
              this.$message.success("验证码发送成功！");
              this.isCountDownShow=true;
            } else {
              this.isCountDownShow=false;
              this.$message.error("验证码发送失败！");
            }
          });
    },
     // 倒计时
     startCountDown() {
      this.countDownSecond = 60;
      timer = setInterval(() => {
        this.countDownSecond--;
        if (this.countDownSecond == 0) {
          clearInterval(timer);
          this.isCountDownShow = false;
        }
      }, 1000);
    },
  }
}
</script>

<style  lang="stylus" scoped>

.resetWrapper {
  background-image: url("~@/assets/xt.jpg");
  width:100%;
  height: 100%;
  position: fixed;
  background-size: 100% 100%;
}
.box-card{
  width: 430px;
  margin-left:30px;
  margin-top: 80px;
  height: 350px;
}
  .formWrapper {
    text-align: center;
  }
  
  .resetTitle {
    margin-bottom: 10px;
    font-weight: 300;
    font-size: 30px;
    color: #000;
  }

</style>